<template>
  <div class="icons">
    <swiper  :options="swiperOption" >
      <swiper-slide v-for="(page,index) in pages" :key = 'index'>
        <div class="icon" v-for="item in page" :key="item.key">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl" alt="">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
  export default {
    name: 'HomeIcons',
    data (){
      return {
        swiperOption:{
          autoplay:false
        }
      }
    },
    props:{
      list:Array
    },
    computed:{
      pages(){
        const pages = [];
        this.list.forEach((item,index)=>{
          const page  = Math.floor(index/8);
          if(!pages[page]){
            pages[page] = []
          }
          pages[page].push(item);
        })
        return pages
      }
    }
  }
</script>

<style scoped lang="stylus">
  @import "~Styles/varibles.styl"
  @import "~Styles/mixins.styl"
  .icons >>> .swiper-container
    height:0
    padding-bottom:50%
  .icons
    margin-top: .1rem
    .icon
      overflow: hidden
      position: relative
      float: left
      width: 25%
      height: 0
      padding-bottom: 25%
      .icon-img
        position: absolute
        top: 0
        right: 0
        left:0
        bottom: .44rem
        box-sizing:border-box
        padding .1rem
        .icon-img-content
          display: block
          margin: 0 auto
          height: 100%
      .icon-desc
        position: absolute
        left: 0
        right: 0
        bottom: 0
        height:.44rem
        line-height: .44rem
        text-align: center
        color: $darkTextColor
        ellipsis()

</style>



